function Blog(id, title, cTitle, content, cContent, date, cDate, cover){
  this.id = id;
  this.title = title;
  this.cTitle = cTitle;
  this.content = content;
  this.cContent = cContent;
  this.date = date;
  this.cDate = cDate;
  this.cover = cover;
}

export function placeholder() {
  return new Blog("Blog PlaceHolder", "", "I am so happy, my dear friend, so absorbed in the exquisite sense of mere tranquil existence, that I neglect my talents. I should be incapable of drawing since", "", "1st Jan 1970", "1970  1  1 ", "@/images/placeholder.png");
}

